import React from 'react';
import PropTypes from 'prop-types';
import { Button } from 'react-style-guide';

function CrossDeviceCodeInputModule({ translate, handleCodeChange, handleCodeSubmit, codeValue }) {
  return (
    <div className='enter-code-container'>
      <h3>{translate('Heading.LoginAnotherDevice')}</h3>
      <div className='enter-code-container-left' />
      <div className='enter-code-container-right'>
        <br />
        <h3 className='enter-code-warning-text'>{translate('Heading.LoginWarning')}</h3>
        <br />
        <ul className='enter-code-list'>
          <li>{translate('Label.Warning1')}</li>
          <li>{translate('Label.Warning2')}</li>
        </ul>
      </div>
      <form onSubmit={event => handleCodeSubmit(event)}>
        <div>
          <input
            className='enter-code-input input-field form-control'
            type='text'
            placeholder={translate('Label.EnterCode')}
            onChange={event => handleCodeChange(event.target.value)}
            value={codeValue}
          />
        </div>
        <div className='enter-code-input-container-right'>
          <Button
            type='submit'
            className='btn-control-sm enter-code-button'
            isDisabled={codeValue.length <= 0}>
            {translate('Action.Enter')}
          </Button>
        </div>
      </form>
    </div>
  );
}

CrossDeviceCodeInputModule.propTypes = {
  translate: PropTypes.func.isRequired,
  handleCodeChange: PropTypes.func.isRequired,
  handleCodeSubmit: PropTypes.func.isRequired,
  codeValue: PropTypes.string.isRequired
};

export default CrossDeviceCodeInputModule;
